*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --white:#FFFFFF;

  --gray-100:#ededf1;
  --gray-300:#a8a8b3;
  --gray-900:#121214;

}


@media(max-width:1080px){
  html{
    font-size:93.75%;
  }
}
@media(max-width:720px){
  html{
    font-size:87.5%;
  }
}
body{
background-color: var( --gray-100);
color: var( --white)
}

body, input ,textarea, select, button{
  font: 400 1rem 'Roboto', sans-serif
}

button{
  cursor: pointer;
}

a{
  color: inherit;
  text-decoration:none;
}