.container{max-width:1120px;margin:0 auto;padding:0 2rem;height:calc(100vh - 5rem);display:flex;align-items:center;justify-content:center;flex-direction:column;gap:5rem}.container .weatherInformation hr{margin:1rem 0}.container .weatherInformation .city​​Weather h1{color:var(--gray-900);text-align:center;margin-bottom:1rem}.container .weatherInformation .city​​Weather p{color:var(--gray-300);text-align:center}.container .weatherInformation .temperatureClimate{display:flex;justify-content:space-around;align-items:baseline;color:var(--gray-900)}.container .weatherInformation .temperatureClimate div{display:flex;flex-direction:column;align-items:center;margin:0 2rem}.container .weatherInformation .temperatureClimate .currentTemperature h1{text-transform:capitalize}.container .weatherInformation .dayInfo{display:grid;grid-template-columns:repeat(2, 1fr);gap:3px;color:var(--gray-900);width:14rem;margin:auto;font-weight:600}.container .searchSection{display:flex;flex-direction:column;align-items:center}.container .searchSection input{width:20rem;margin-bottom:2rem;height:3rem;border:2px solid var(--gray-300);border-radius:5px;text-align:center}.container .searchSection input::-webkit-input-placeholder{text-align:center}.container .searchSection button{width:7rem;height:3rem;background-color:var(--gray-900);color:var(--white);border-radius:5px;border:none;transition:filter .2s}.container .searchSection button:hover{filter:brightness(0.8)}